.blue {
  color: #1f70fb;
}
.blueRound {
  display: inline-block;
  vertical-align: middle;
  width: 6px;
  height: 6px;
  background: #1f70fb;
  border-radius: 50%;
  margin-right: 6px;
}
.searchBox {
  width: 656px;
  margin: 0 auto;
  margin-top: 14px;
  font-size: 0;
}
.searchBox .int {
  display: inline-block;
  vertical-align: middle;
  width: 584px;
  height: 44px;
  background: #ffffff;
  border-radius: 4px 0px 0px 4px;
  padding: 0 16px;
  outline: none;
  border: none;
  font-size: 14px;
}
.searchBox .sBtn {
  display: inline-block;
  vertical-align: middle;
  width: 72px;
  height: 44px;
  line-height: 44px;
  text-align: center;
  background: #1f70fb;
  opacity: 1;
  border-radius: 0px 4px 4px 0px;
  font-size: 14px;
  color: white;
}
.resourceBox {
  width: 1200px;
  height: 695px;
  height: calc(100% - 160px);
  margin: 0 auto;
  margin-top: 30px;
  background: #ffffff;
  border-radius: 4px;
}
.resourceBox .res-tit {
  height: 60px;
  line-height: 60px;
  display: flex;
  justify-content: space-between;
  padding: 0 16px 0 40px;
  border-bottom: 1px solid #F0EFF2;
}
.resourceBox .res-tit .tLeft {
  color: #9aa2b5;
}
.resourceBox .res-list {
  height: calc(100% - 60px);
  overflow: auto;
}
.resourceBox .resUl li {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 30px 20px 40px;
  border-bottom: 1px solid #F0EFF2;
}
.resourceBox .resUl li .liLeft .p1 {
  font-size: 18px;
  font-weight: bold;
  color: #4e5666;
}
.resourceBox .resUl li .liLeft .p1 a {
  font-size: 18px;
  font-weight: bold;
  color: #4e5666;
}
.resourceBox .resUl li .liLeft .p1 a:hover {
  color: #4e5666;
}
.resourceBox .resUl li .liLeft .p2 {
  font-size: 14px;
  color: #9aa2b5;
}
.resourceBox .resUl li .liLeft .p2 a {
  font-size: 14px;
  color: #9aa2b5;
}
.resourceBox .resUl li .liLeft .p2 a:hover {
  color: #9aa2b5;
}
.resourceBox .resUl li .liRight span {
  margin-left: 20px;
}
.resourceBox .resUl li .liRight span i {
  margin-right: 6px;
}
.resourceBox .resUl li .liRight .sp1 {
  display: inline-block;
  vertical-align: middle;
  min-width: 80px;
}
.resourceBox .resUl li .liRight .sp2 {
  display: inline-block;
  vertical-align: middle;
  min-width: 120px;
}
.lore-tit {
  height: 60px;
  line-height: 60px;
  padding: 0 24px;
  border-bottom: 1px solid #F0EFF2;
}
.lore-tit h6 {
  display: inline-block;
  padding-left: 8px;
  border-left: 3px solid #4C84FF;
  font-size: 14px;
  font-weight: bold;
}
.loreUl {
  padding: 50px 0 0 55px;
}
.loreUl li {
  width: 240px;
  height: 260px;
  background: #f1fff9;
  border: 1px solid #d4e1e9;
  border-radius: 2px;
  float: left;
  margin: 0 40px 50px 0;
  box-shadow: 0px 3px 6px rgba(0, 96, 183, 0.14);
}
.loreUl li:hover {
  box-shadow: 0px 3px 10px rgba(0, 96, 183, 0.3);
}
.loreUl li a {
  display: block;
  width: 100%;
  height: 100%;
  padding: 20px 10px 0;
  text-align: center;
}
.loreUl li .lo-img {
  display: inline-block;
  margin: 0 auto;
  width: 110px;
  height: 110px;
  border-radius: 50%;
  overflow: hidden;
}
.loreUl li .lo-img img {
  width: 100%;
}
.loreUl li .p1 {
  margin-top: 5px;
  font-size: 18px;
  font-weight: bold;
  color: #4e5666;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.loreUl li .p2 {
  margin-top: 10px;
  font-size: 14px;
  font-weight: 400;
  color: #8fa1b5;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.loreUl li .p3 {
  margin-top: 10px;
  font-size: 14px;
  color: #1f70fb;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.loreBox {
  width: 1200px;
  height: 695px;
  height: calc(100% - 90px);
  margin: 0 auto;
  margin-top: 30px;
  background: #ffffff;
  border-radius: 4px;
}
.loreBox .lore-list {
  height: calc(100% - 60px);
  overflow: auto;
}
.listBox-wrap {
  width: 1200px;
  height: 100%;
  margin: 0 auto;
}
.list-box .resourceBox {
  width: 900px;
  height: calc(100% - 70px);
  float: left;
  margin-top: 0;
}
.list-box .resourceBox .res-tit {
  padding: 23px 24px 0 24px;
}
.list-box .resourceBox .res-tit h6 {
  padding-left: 8px;
  font-size: 14px;
  font-weight: bold;
  border-left: 3px solid #4C84FF;
}
.list-box .detailBox {
  height: calc(100% - 10px);
}
.list-box .detailBox .res-des {
  position: relative;
  height: calc(100% - 60px);
  overflow: auto;
  padding: 22px 0;
}
.list-box .detailBox .res-des .desIn {
  padding: 0 40px;
}
.list-box .detailBox .res-des .de-tit {
  font-size: 24px;
  font-weight: bold;
  color: #4e5666;
}
.list-box .detailBox .res-des .de-record {
  margin-top: 10px;
  padding-bottom: 20px;
  border-bottom: 1px solid #F0EFF2;
}
.list-box .detailBox .res-des .de-record span {
  margin-right: 25px;
  font-size: 14px;
  font-weight: 400;
  color: #9aa2b5;
}
.list-box .detailBox .res-des .de-record span:last-child {
  margin-right: 0;
}
.list-box .detailBox .res-des .de-record span i {
  margin-right: 5px;
}
.list-box .detailBox .res-des .de-wen {
  padding-bottom: 80px;
}
.list-box .detailBox .res-des .de-wen p {
  margin-top: 15px;
  text-indent: 30px;
  line-height: 24px;
  font-size: 14px;
  font-weight: 400;
  color: #4e5666;
}
.list-box .detailBox .res-des .bot-help {
  position: fixed;
  display: flex;
  justify-content: space-between;
  bottom: 30px;
  width: 900px;
  height: 72px;
  line-height: 72px;
  background: #ffffff;
  box-shadow: 0px -4px 14px rgba(154, 162, 181, 0.3);
  opacity: 1;
  border-radius: 4px;
  padding: 0 40px;
}
.list-box .questionBox {
  float: right;
  width: 280px;
  height: calc(100% - 10px);
  opacity: 1;
}
.list-box .questionBox .que-tit {
  height: 60px;
  line-height: 60px;
  padding: 0 24px;
  border-bottom: 1px solid #F0EFF2;
  background: white;
  border-radius: 4px 4px 0 0;
}
.list-box .questionBox .que-tit h6 {
  padding-left: 8px;
  font-size: 14px;
  font-weight: bold;
  border-left: 3px solid #4C84FF;
  display: inline-block;
}
.list-box .questionBox .que-list {
  height: calc(100% - 60px);
  overflow: auto;
}
.list-box .questionBox .queUl {
  background: white;
  border-radius: 0 0 4px 4px;
}
.list-box .questionBox .queUl li {
  padding: 20px 24px;
  border-bottom: 1px solid #F0EFF2;
}
.list-box .questionBox .queUl li:last-child {
  border-bottom: none;
}
.list-box .questionBox .queUl li .p1 a {
  font-size: 18px;
  font-weight: bold;
  color: #4e5666;
}
.list-box .questionBox .queUl li .p1 a:hover {
  color: #4e5666;
}
.list-box .questionBox .queUl li .p2 {
  display: block;
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  text-overflow: ellipsis;
}
.list-box .questionBox .queUl li .p2 a {
  font-size: 14px;
  color: #9aa2b5;
}
.list-box .questionBox .queUl li .p2 a:hover {
  color: #9aa2b5;
}
.qa-box .problemBox {
  float: left;
  width: 280px;
  height: calc(100% - 10px);
  overflow: auto;
}
.qa-box .problemBox .proUl {
  width: 100%;
  background: #ffffff;
  border-radius: 4px;
  padding: 20px;
}
.qa-box .problemBox .proUl li.active a {
  background: rgba(31, 112, 251, 0.2);
  border-radius: 4px;
  color: #1f70fb;
}
.qa-box .problemBox .proUl li a {
  display: block;
  padding: 16px 17px;
  font-size: 14px;
  color: #4e5666;
}
.qa-box .explainBox {
  float: right;
  height: calc(100% - 10px);
}
.qa-box .explainBox .res-tit h6 {
  font-weight: bold;
  color: #4e5666;
}
.qa-box .explainBox .exp-des {
  height: calc(100% - 60px);
  overflow: auto;
  padding: 24px;
}
.qa-box .explainBox .expUl li {
  margin-bottom: 20px;
  padding: 15px 20px;
  background: #f1f5ff;
  border: 1px solid #d4e1e9;
  border-radius: 2px;
}
.qa-box .explainBox .expUl li:last-child {
  margin-bottom: 0;
}
.qa-box .explainBox .expUl li .ex-tit {
  display: flex;
  font-size: 18px;
  font-weight: bold;
  color: #4e5666;
}
.qa-box .explainBox .expUl li .ex-tit .ic-question {
  display: inline-block;
  vertical-align: middle;
  width: 20px;
  height: 20px;
  background: url('../images/ic-question.png') no-repeat center center;
  margin: -2px 10px 0 0;
}
.qa-box .explainBox .expUl li .ex-wen {
  display: flex;
  padding-left: 32px;
  margin-top: 10px;
}
.qa-box .explainBox .expUl li .ex-wen .w1 {
  width: 39px;
}
.qa-box .explainBox .expUl li .ex-wen .w1 i {
  display: inline-block;
  width: 16px;
  height: 16px;
  line-height: 14x;
  text-align: center;
  border-radius: 50%;
  background: #9AA2B4;
  color: white;
  font-size: 12px;
}
